html, body
{
	margin: 0;
	padding: 0;
	background-color: #1bade9;
}
canvas{
	display:block;
	margin:0 auto;
}
.slider-de-overlays {
	margin:0 auto;
    width: 475px;
    img{
    	margin: 0 auto;
    }
}
.slick-prev:before,
.slick-next:before {
    font-family: "slick";
    font-size: 20px;
    line-height: 1;
    color: red;
    opacity: .75;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}
.slick-prev {
    left: -25px
}
[dir="rtl"] .slick-prev {
    left: auto;
    right: -25px
}
.slick-prev:before {
    // content: "←"
    content: url('../../img/flecha-izq.png');
    position: absolute;
    left: -15px;

}
[dir="rtl"] .slick-prev:before {
    content: url('../../img/flecha-der.png');
}
.slick-next {
    right: -25px
}
[dir="rtl"] .slick-next {
    left: -25px;
    right: auto
}
.slick-next:before {
    // content: "→"
    content: url('../../img/flecha-der.png');
}
[dir="rtl"] .slick-next:before {
    content: url('../../img/flecha-izq.png');
}
